.fixed-plugin {
  width: 64px;
  z-index: 1031;
  text-align: center;
  background: #0000004d;
  border-radius: 8px 0 0 8px;
  position: absolute;
  top: 150px;
  right: 0;
}

.fixed-plugin .fa-cog {
  color: #fff;
  border-radius: 0 0 6px 6px;
  padding: 10px;
}

.fixed-plugin .dropdown-menu {
  width: 260px;
  border-radius: 10px;
  left: auto;
  right: 80px;
}

.fixed-plugin .dropdown-menu:after, .fixed-plugin .dropdown-menu:before {
  margin-left: auto;
  left: auto;
  right: 10px;
}

.fixed-plugin .fa-circle-thin {
  color: #fff;
}

.fixed-plugin .active .fa-circle-thin {
  color: #0bf;
}

.footer-dropdown {
  top: -120px !important;
}

.footer-dropdown:before, .footer-dropdown:after {
  top: 300px !important;
}

.fixed-plugin .dropdown-menu > .active > a, .fixed-plugin .dropdown-menu > .active > a:hover, .fixed-plugin .dropdown-menu > .active > a:focus {
  color: #777;
  text-align: center;
}

.fixed-plugin img {
  width: 230px;
  max-height: 175px;
  border-radius: 6px;
  margin: 0 auto;
}

.fixed-plugin .btn:hover {
  color: #fff;
}

.fixed-plugin .badge {
  cursor: pointer;
  height: 23px;
  width: 23px;
  border: 3px solid #fff;
  border-radius: 50%;
  margin-right: 5px;
  display: inline-block;
  position: relative;
}

.fixed-plugin .badge.active, .fixed-plugin .badge:hover {
  border-color: #0bf;
}

.fixed-plugin .badge-white {
  background-color: #fff;
}

.fixed-plugin .badge-blue {
  background-color: #4087ea;
}

.fixed-plugin .badge-azure {
  background-color: #36afec;
}

.fixed-plugin .badge-green {
  background-color: #53a319;
}

.fixed-plugin .badge-orange {
  background-color: #ffb33b;
}

.fixed-plugin .badge-red {
  background-color: #ed362c;
}

.fixed-plugin h5 {
  margin: 10px;
  font-size: 14px;
}

.fixed-plugin .dropdown-menu li {
  padding: 5px 10px;
  display: block;
}

.fixed-plugin li.adjustments-line, .fixed-plugin li.header-title {
  width: 100%;
  height: 50px;
  min-height: inherit;
}

.fixed-plugin li.header-title {
  height: 30px;
  text-transform: uppercase;
  padding: 0 15px;
  font-size: 12px;
  font-weight: 600;
  line-height: 40px;
}

.fixed-plugin .adjustments-line p {
  float: left;
  margin-bottom: 0;
  font-size: 1em;
  display: inline-block;
}

.fixed-plugin .adjustments-line .switch {
