}

.fixed-plugin .dropdown-menu > li.adjustments-line > a {
  border-bottom: 1px solid #ddd;
  margin-left: 10px;
  margin-right: 10px;
  padding-left: 0;
  padding-right: 0;
}

.fixed-plugin .dropdown-menu > li > a.img-holder {
  text-align: center;
  opacity: 1;
  cursor: pointer;
  background-color: #fff;
  border: 1px solid #e4e4e4;
  border-radius: 10px;
  padding-left: 0;
  padding-right: 0;
  font-size: 16px;
}

.fixed-plugin .dropdown-menu > li > a.switch-trigger:hover, .fixed-plugin .dropdown-menu > li > a.switch-trigger:focus {
  background-color: #0000;
}

.fixed-plugin .dropdown-menu > .active > a.img-holder, .fixed-plugin .dropdown-menu > .active > a.img-holder {
  background-color: #fff;
  border-color: #0bf;
}

.fixed-plugin .dropdown-menu > li > a img {
  margin-top: auto;
}

.dropdown-menu > li > a:hover, .dropdown-menu > li > a:focus {
  opacity: 1;
}

.has-switch {
  cursor: pointer;
  text-align: left;
  width: 60px;
  user-select: none;
  -o-user-select: none;
  border-radius: 30px;
  line-height: 1.72222;
  display: inline-block;
  position: relative;
  overflow: hidden;
}

.has-switch.deactivate {
  opacity: .5;
  filter: alpha(opacity= 50);
  cursor: default !important;
}

.has-switch.deactivate label, .has-switch.deactivate span {
  cursor: default !important;
}

.has-switch > div {
  width: 100px;
  position: relative;
  top: 0;
}

.has-switch > div.switch-animate {
  transition: left .25s ease-out;
}

.has-switch > div.switch-off {
  left: -35px;
}

.has-switch > div.switch-on {
  left: 0;
}

.has-switch > div label {
  background: -o-linear-gradient(top, #fff 0%, #f1f1f2 100%);
  background: -ms-linear-gradient(top, white 0%, #f1f1f2 100%);
  cursor: pointer;
  background: linear-gradient(#fff 0%, #f1f1f2 100%);
  box-shadow: inset 0 1px 1px #fff, 0 1px 1px #00000040;
}

.has-switch input[type="checkbox"] {
  display: none;
}

.has-switch span {
  cursor: pointer;
  float: left;
  height: 24px;
  text-align: center;
  text-indent: -10px;
  width: 50%;
  z-index: 1;
  margin: 0;
  padding-top: 5px;
  padding-bottom: 6px;
  font-size: 11px;
  font-weight: bold;
  line-height: 15px;
  transition: all .25s ease-out;
  position: relative;
}

.has-switch span.switch-left {
  color: #fff;
  background-color: #2ca8ff;
  border-left: 1px solid #0000;
  border-radius: 30px 0 0 30px;
}

.has-switch .switch-off span.switch-left {
  background-color: #ddd;
}

.has-switch span.switch-right {
  color: #fff;
  text-indent: 1px;
  background-color: #2ca8ff;
  border-radius: 0 30px 30px 0;
}

.has-switch .switch-off span.switch-right {
  background-color: #ddd;
}

.has-switch label {
  float: left;
  height: 22px;
  vertical-align: middle;
  width: 22px;
  z-index: 100;
  border-radius: 12px;
  margin: 1px -13px;
  padding: 0;
  transition: all .25s ease-out;
  position: relative;
}

.has-switch .switch-on .fa-check:before {
  margin-left: 10px;
}

.has-switch:hover .switch-on label {
  width: 26px;
  margin: 1px -17px;
}

.has-switch:hover .switch-off label {
  width: 26px;
  margin: 1px -13px;
}

.logo-container {
  z-index: 3;
